.navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 2rem;
    position: relative;
    align-items: center;
    background-color: $color-black;
    

    .hamIcon{
        display: none;
        color: white;

        @include respond(tab-port){
            display: block;
            font-size: 4rem;
            z-index: 999999;
         }

         @include respond(phone){
              font-size: 2rem;
        }
    }
    img{
        width: 17rem;
        height: auto;
        z-index: 999999;

        @include respond(tab-land){
            width: 13rem;
        }

        @include respond(tab-port){
            width: 11rem;
         }


        @include respond(phone){
            width: 10rem;
            
        }
    }

    &__list{
        width: 50%;
        display: flex;
        justify-content: space-evenly;

        @include respond(tab-land){
            width: 60%;
        }

        @include respond(tab-port){
            position: absolute;
            top: -1400%;
            right: 0%;
            width: 100%;
            background-color: rgb(32, 32, 32);
            z-index: 9999;
            padding: 4rem;
            height: 85rem;
            align-items: center;
            flex-direction: column;
         }
        &__item{
            font-size: 1.8rem;
            font-weight: 400;
            font-family: $fontAudio;
            color: $color-blue;
            text-decoration: none;

            @include respond(tab-land){
               font-size: 1.4rem;
            }
            @include respond(tab-port){
                font-size:3rem;
                margin: 2rem;
             }
            &:hover{
                color: wheat;
            }
        }
    }
}