.header {
    width: 100%;
    background-color: $color-black;
    height: auto;
    position: relative;
    padding: 5rem;
    display: flex;
    align-items: center;



    img {
        width: 60%;
        transform: scale(1);
        height: auto;
        animation: imgScale 10s linear 2s infinite alternate;

        @include respond(tab-land){
            width:50%;
        }

        @include respond(tab-port){
            width:100%;
            opacity: .5;
        }
    }

    h1{
        position: absolute;
        width:40%;
        right: 3%;
        text-align: right;
        font-weight: bold;
        letter-spacing: .2rem;
        line-height: 1.2;
        transform: scale(.9);
        color:$color-blue;
        font-size: 8rem;
        text-shadow: 0 .2rem 2rem rgba(255, 255, 255, 0.219);
        animation: textScale 15s linear 2s infinite alternate;
        
        @include respond(tab-land){
            width:50%;
            font-size: 6rem;
        }

        @include respond(tab-port){
            width:80%;
            text-align: center;
            font-size: 7rem;
        }
        @include respond(phone){
            font-size: 4rem;
            line-height: 1.3;
            text-align: center;
        }
        span{
            color: white;
          
        }
    }


}





//Animation
@keyframes imgScale {
    from {
        transform: scale(.9);
    }

    to {
        transform: scale(1);
    }
}

@keyframes textScale {
    from {
        right: 5%;
    }

    to {
        right: 15%;
    }
}